export default Standings;

export const pageQuery = graphql`
{
  allGamesJson {
    edges {
      node {
        id
        year
        winner
        week
        team_a {
          id
          logo
          name
          owner
          ownerId
          rank
          score
        }
        team_b {
          id
          logo
          name
          owner
          ownerId
          rank
          score
        }
        playoffTierType
      }
    }
  }
  allOwnersJson {
    edges {
      node {
        fantasyname
        id
        image
        name
        status
        testName
        user
      }
    }
  }
}
`;
